import {
    Component,
    ViewChild,
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    OnDestroy,
    ElementRef,
    Input,
    Output,
    EventEmitter,
    Renderer2,
    NgZone,
    OnChanges,
    SimpleChanges,
    Inject,
} from '@angular/core';
import { License } from '@imunify360-api/license';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';

const UPGRADE_PAGE_URL = 'https://imunify360.com/imunify-widgets/';

@Component({
    selector: 'i360-upgrade-iframe',
    templateUrl: './upgrade-iframe.component.html',
    styleUrls: ['./upgrade-iframe.component.scss'],
    host: {
        '[style.visibility]': 'isReady ? "visible" : "hidden"'
    },
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class UpgradeIframeComponent implements OnDestroy, OnChanges {
    @Input() license: License;
    @Input() priceFull: string;
    @Input() lang: string;
    @Input() iaidToken: string | undefined;
    @Output() iframeLoaded = new EventEmitter<void>();

    @ViewChild('upgradeIframe', { static: false }) upgradeIframe: ElementRef<HTMLIFrameElement>;
    upgradeIframeSrc: SafeUrl;

    isReady: boolean = false;

    private stopListening: () => void;

    constructor(
        private detector: ChangeDetectorRef,
        private sanitizer: DomSanitizer,
        private renderer: Renderer2,
        private ngZone: NgZone,
        @Inject('Window') private window: Window
    ) {
        this.listenMessageFromIframe();
    }

    ngOnChanges(changes: SimpleChanges): void {
        if ('lang' in changes) {
            this.isReady = false;
            this.upgradeIframeSrc = this.lang ? this.getIframeUrl(this.lang) : '';
        }
    }

    ngOnDestroy(): void {
        this.stopListening();
    }

    private listenMessageFromIframe(): void {
        this.stopListening =
            this.renderer.listen('window', 'message', event => {
                this.handleWindowMessage(event);
            });
    }

    private handleWindowMessage(event): void {
        if (event.data === 'UpgradePageLoaded') {
            this.ngZone.run(() => {
                this.postIframeData();
                this.isReady = true;
                this.detector.detectChanges();
                this.iframeLoaded.emit();
            });
        }
    }

    private postIframeData(): void {
        const iframeWindow = this.upgradeIframe?.nativeElement?.contentWindow;
        if (iframeWindow) {
            const location = this.window.location;
            let upgrade_url = this.license.upgrade_url;
            let upgrade_url_360 = this.license.upgrade_url_360;

            if (upgrade_url?.startsWith('../')) {
                upgrade_url = `${location.origin}${location.pathname}/../${upgrade_url}`;
            }

            if (upgrade_url_360?.startsWith('../')) {
                upgrade_url_360 = `${location.origin}${location.pathname}/../${upgrade_url_360}`;
            }

            const license = {
                product: this.license.product,
                pricing: {
                    im_av: {
                        standard: this.license.pricing?.im_av ? this.license.pricing.im_av.standard : null
                    },
                    im_360: {
                        priceFull: this.priceFull
                    }
                },
                upgrade_url,
                upgrade_url_360,
            };
            iframeWindow.postMessage(JSON.stringify({ license, token: this.iaidToken }), '*');
        }
    }

    private getIframeUrl(lang: string): SafeUrl {
        const url = `${UPGRADE_PAGE_URL}?lang=${lang}`;
        return this.sanitizer.bypassSecurityTrustResourceUrl(url);
    }
}
