import { Component, ViewChild, ChangeDetectionStrategy, ChangeDetectorRef, OnDestroy, AfterViewInit } from '@angular/core';
import { LicenseState } from 'app/services/license-state';
import { LoaderDirective } from 'app/utils/loader/loader.directive';
import { LicensePriceCounts, LicensePricing, Product, License } from '@imunify360-api/license';
import { Subject, noop, combineLatest, takeUntil } from 'rxjs';
import { TranslateService } from '@ngx-translate/core';
import { AuthCloudService } from 'app/services/auth-cloud';

export const UPGRADE_PAGE_LOAD_TIMEOUT = 5000;

@Component({
    templateUrl: './upgrade.component.html',
    styleUrls: ['./upgrade.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class UpgradeComponent implements OnDestroy, AfterViewInit {
    Product = Product;
    @ViewChild(LoaderDirective, {static: true}) loader: LoaderDirective;
    priceFull?: string;
    license: License;
    iaidToken: string | undefined;
    lang: string;
    iframeLoadingTimeout = false;
    isIframeLoaded = false;
    private destroy$ = new Subject<void>();
    private iframeTimeoutId;

    constructor(
        private detector: ChangeDetectorRef,
        private licenseState: LicenseState,
        private translateService: TranslateService,
        private authCloudService: AuthCloudService,
    ) {
        this.lang = this.translateService.currentLang;
        this.loadData();
        this.waitIframe();
        this.listenLangChange();
    }

    ngAfterViewInit(): void {
        this.loader.show('upgrade');
    }

    ngOnDestroy(): void {
        this.destroy$.next();
        clearTimeout(this.iframeTimeoutId);
    }

    buy(upgradeUrl: string): void {
        window.open(upgradeUrl);
    }

    onIframeLoaded(): void {
        this.isIframeLoaded = true;
        clearTimeout(this.iframeTimeoutId);
        this.loader.hide('upgrade');
        this.detector.markForCheck();
    }

    private loadData(): void {
        const iaidToken$ = this.authCloudService.getIaidToken();

        combineLatest([iaidToken$, this.licenseState.license]).pipe(
            takeUntil(this.destroy$)
        ).subscribe({
            next: ([iaidToken, license]) => {
                this.setLicense(license as License);
                this.iaidToken = iaidToken;
                this.detector.markForCheck();
            },
            error: noop
        });
    }

    private listenLangChange(): void {
        this.translateService.onLangChange
            .pipe(takeUntil(this.destroy$))
            .subscribe({
                next: value => {
                    this.lang = value.lang;
                    this.iframeLoadingTimeout = false;
                    this.isIframeLoaded = false;
                    this.loader.show('upgrade');
                    this.waitIframe();
                    this.detector.markForCheck();
                },
                error: noop
            });
    }

    private setLicense(license: License): void {
        this.license = license;
        this.priceFull = this.getFullPrice(license);
    }

    private getFullPrice(license: License): string | undefined {
        const pricing = license.pricing;
        if (pricing) {
            const count = license.user_count || 1;
            return UpgradeComponent.getPrice(pricing, count);
        }
    }

    private waitIframe(): void {
        if (this.iframeTimeoutId) clearTimeout(this.iframeTimeoutId);

        this.iframeTimeoutId = setTimeout(() => {
            if (!this.isIframeLoaded) {
                this.loader.hide('upgrade');
                this.iframeLoadingTimeout = true;
                this.detector.markForCheck();
            }
        }, UPGRADE_PAGE_LOAD_TIMEOUT);
    }

    static getPrice(pricing: LicensePricing, count: number): string | undefined {
        if (pricing) {
            for (const i of LicensePriceCounts) {
                const entry = pricing['im_360_' + String(i)];
                if (entry && count <= i) {
                    return entry.standard;
                }
            }

            return pricing?.im_360_unlimited?.standard;
        }
    }
}
