import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router,
    RouterStateSnapshot, UrlTree } from '@angular/router';
import { AuthState } from 'app/services/auth-state';

@Injectable()
export class AdminGuard implements CanActivate {
    constructor(private authState: AuthState, private router: Router) {}
    canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean | UrlTree {
        if (this.authState.isClient.value) {
            this.router.navigate(['/', 'AV', 'malware', 'files']);
            return false;
        }
        return true;
    }
}
