import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { FeatureName } from '@imunify360-api/features-management';
import { MalwareHistoryComponent } from 'app/components/malware/history/history.component';
import { IgnoreListComponent } from 'app/components/malware/ignore-list/ignore-list.component';
import { MaliciousComponent } from 'app/components/malware/malicious/malicious.component';
import { UserListComponent } from 'app/components/malware/user-list/user-list.component';
import {
    FeaturesManagementWrapperComponent,
} from 'app/components/settings/features-management/wrapper/wrapper.component';
import { UserFeaturesStatusGuard } from 'app/core/user-features-status.guard';
import { SettingsComponent } from 'app/components/av/settings/general/settings.component';
import { UpgradeComponent } from './upgrade/upgrade.component';
import { UpgradeGuard } from './guards/upgrade-guard';
import { RootComponent } from 'app/components/av/root/root.component';
import { SUPPORT_ROUTE } from 'app/components/support/route';
import { UnsavedConfigRouteGuard } from 'app/core/unsaved-route-guard';
import { SettingsIndexAVComponent } from 'app/components/av/settings/settings-index-av.component';
import { FiltersService } from 'app/services/filters';
import { MalwareIndexComponent } from '../malware/malware-index.component';
import { MalwareOnDemandScanComponent } from '../malware/on-demand/on-demand-scan.component';
import { UpgradeNeededComponent } from '../../upgrade-needed/upgrade-needed.component';
import { SETTINGS_NOTIFICATION_ROUTE } from '../settings/notifications/route';
import { EULA_ROUTE } from 'app/components/eula/eula.module';
import { AuthService } from 'app/services/auth';


const routes: Routes = [
    {
        path: 'admin',
        component: RootComponent,
        canActivate: [AuthService],
        children: [
            {
                path: '',
                pathMatch: 'full',
                redirectTo: 'malware',
            },
            {
                path: 'malware',
                component: MalwareIndexComponent,
                children: [
                    {path: '', redirectTo: 'user-list', pathMatch: 'full'},
                    {
                        path: 'user-list',
                        component: UserListComponent,
                        canActivate: [FiltersService],
                        runGuardsAndResolvers: 'paramsOrQueryParamsChange',
                    },
                    {
                        path: 'files',
                        component: MaliciousComponent,
                        canActivate: [FiltersService],
                        runGuardsAndResolvers: 'paramsOrQueryParamsChange',
                    },
                    {
                        path: 'on-demand-scan',
                        component: MalwareOnDemandScanComponent,
                        canActivate: [FiltersService],
                        runGuardsAndResolvers: 'paramsOrQueryParamsChange',
                    },
                    {
                        path: 'history',
                        component: MalwareHistoryComponent,
                        canActivate: [FiltersService],
                        runGuardsAndResolvers: 'paramsOrQueryParamsChange',
                    },
                    {
                        path: 'ignore-list',
                        component: IgnoreListComponent,
                        canActivate: [FiltersService],
                        runGuardsAndResolvers: 'paramsOrQueryParamsChange',
                    },
                ],
                canActivate: [FiltersService],
                runGuardsAndResolvers: 'always',
            },
            {
                path: SUPPORT_ROUTE.path,
                loadChildren: SUPPORT_ROUTE.loadChildren,
            },
            {
                path: 'settings',
                component: SettingsIndexAVComponent,
                children: [
                    {path: '', redirectTo: 'general', pathMatch: 'full'},
                    {
                        path: 'general',
                        component: SettingsComponent,
                        canDeactivate: [UnsavedConfigRouteGuard],
                    },
                    SETTINGS_NOTIFICATION_ROUTE,
                    {
                        path: 'features-management',
                        component: FeaturesManagementWrapperComponent,
                        canActivate: [FiltersService],
                        runGuardsAndResolvers: 'paramsOrQueryParamsChange',
                    },
                ],
            },
            {
                path: 'upgrade',
                component: UpgradeComponent,
                canActivate: [UpgradeGuard],
            },
            {
                path: 'reputation-management',
                loadChildren: () => import('../reputation/reputation.module')
                    .then(m => m.ReputationModule),
            },
            {
                path: 'firewall',
                component: UpgradeNeededComponent,
                data: { name: 'firewall'},
            },
            {
                path: 'proactive-defense',
                component: UpgradeNeededComponent,
                data: { name: 'pd'},
            },
            {
                path: 'kernelcare',
                component: UpgradeNeededComponent,
                data: { name: 'kernelcare'},
            },
            EULA_ROUTE,
        ],
    },
    {
        path: 'client',
        component: RootComponent,
        canActivate: [AuthService, UserFeaturesStatusGuard],
        canActivateChild: [UserFeaturesStatusGuard],
        children: [
            {
                path: '',
                pathMatch: 'full',
                redirectTo: 'malware',
            },
            {
                path: 'malware',
                component: MalwareIndexComponent,
                children: [
                    {path: '', redirectTo: 'files', pathMatch: 'full'},
                    {
                        path: 'files',
                        component: MaliciousComponent,
                        data: { feature: FeatureName.av },
                        canActivate: [FiltersService],
                        runGuardsAndResolvers: 'paramsOrQueryParamsChange',
                    },
                    {
                        path: 'on-demand-scan',
                        component: MalwareOnDemandScanComponent,
                        data: { feature: FeatureName.av },
                        canActivate: [FiltersService],
                        runGuardsAndResolvers: 'paramsOrQueryParamsChange',
                    },
                    {
                        path: 'history',
                        component: MalwareHistoryComponent,
                        data: { feature: FeatureName.av },
                        canActivate: [FiltersService],
                        runGuardsAndResolvers: 'paramsOrQueryParamsChange',
                    },
                    {
                        path: 'ignore-list',
                        component: IgnoreListComponent,
                        data: { feature: FeatureName.av },
                        canActivate: [FiltersService],
                        runGuardsAndResolvers: 'paramsOrQueryParamsChange',
                    },
                ],
                canActivate: [FiltersService],
                runGuardsAndResolvers: 'always',
            },
        ],
    },
   {
        path: 'client/features-disabled',
        loadChildren: () => import('../features-disabled/features-disabled.module')
            .then(m => m.FeaturesDisabledModule),
    },
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class AvRoutingModule {}
